import axios from "axios"

class WebGroupRTC {
  private params: any = null // 
  private pingTimer: any = null
  private callbacks: any = null // 
  private socket: WebSocket | null = null // WebSocket 
  private reconnectAttempts: number = 0 // 
  private maxReconnectAttempts: number = 3 // 
  private reconnectDelay: number = 1500 // 
  private sourceArr: any = []
  constructor(params: any, pads: Array<string>, callbacks: any) {
    this.params = params
    this.callbacks = callbacks
    pads.length && this.joinRoom(pads) // 
  }

  //  WebSocket 
  close() {
    clearInterval(this.pingTimer)
    this.sourceArr?.forEach((v: any) => {
      v.cancel()
    })
    this.sourceArr = []
    this.socket?.close()
    this.socket = null
  }

  //  WebSocket 
  sendMessage(message: string) {
    if (this.socket && this.socket.readyState === WebSocket.OPEN) {
      this.socket.send(message)
    }
  }
  startHeartbeat() {
    this.pingTimer = setInterval(() => {
      if (this.socket?.readyState === WebSocket.OPEN) {
        this.socket.send(
          JSON.stringify({
            event: "ping"
          })
        )
        return
      }
      clearInterval(this.pingTimer)
    }, 5000)
  }
  //  WebSocket 
  initSocket(signalServer: string, roomToken: string) {
    const { isWsProxy } = this.params

    let url = `${location.protocol === "https:" ? "wss" : "ws"}://${location.host}/sdk-ws/${roomToken}`

    if (!isWsProxy) {
      url = `${signalServer}/${roomToken}`
    }

    this.socket = new WebSocket(url)

    this.socket.onopen = () => {
      this.reconnectAttempts = 0 // 
      this.startHeartbeat()
    }

    this.socket.onerror = (error: any) => {
      this.callbacks?.onGroupControlError({
        code: error.code || "WS_ERROR",
        msg: error.message || "WebSocket "
      })
      this.handleReconnect(signalServer, roomToken) // 
    }
  }

  //  WebSocket 
  handleReconnect(signalServer: string, roomToken: string) {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      setTimeout(() => {
        this.reconnectAttempts++
        console.log(` ${this.reconnectAttempts} ...`)
        this.initSocket(signalServer, roomToken)
      }, this.reconnectDelay)
    } else {
      console.log("")
      this.callbacks?.onGroupControlError({
        code: "MAX_RECONNECT_ATTEMPTS",
        msg: ""
      })
    }
  }

  // 
  joinRoom(pads: any) {
    const source = axios.CancelToken.source() // 
    this.sourceArr.push(source)
    const { userId, videoStream, uuid, token, manageToken } = this.params

    const url = manageToken ? "/manage/rtc/room/share/applyToken" : `/sdk/rtc/open/room/sdk/share/applyToken`
    const tok = manageToken || token
    axios
      .post(
        url,
        {
          userId,
          uuid,
          terminal: "h5",
          pushPublicStream: false,
          pads: pads.map((padCode: string) => ({
            padCode,
            videoStream,
            userId
          }))
        },
        {
          headers: manageToken ? { Authorization: tok } : { token: tok },
          cancelToken: source.token
        }
      )
      .then((res) => {
        const { signalServer, roomToken } = res?.data?.data
        if (!this.socket) {
          this.initSocket(signalServer, roomToken) //  WebSocket 
        }
      })
      .catch((err) => {
        if (axios.isCancel(err)) {
          return
        }
        const error: any = new Error("")
        error.code = "JOIN_ROOM_ERR"
        this.callbacks?.onGroupControlError({
          code: error.code,
          msg: error.message
        })
      })
  }
}

export default WebGroupRTC
