export class VideoElement {
  private videoDomId: string //  DOM  ID
  private remoteVideoId: string //  ID
  private containerId: string //  ID
  private remoteVideo: HTMLVideoElement // 
  private eventListeners: Array<{ type: string; listener: EventListener; element: HTMLElement }> = [] // 

  constructor(
    private masterIdPrefix: string,
    private remoteUserId: string
  ) {
    //  ID
    this.videoDomId = `${this.masterIdPrefix}_${this.remoteUserId}_armcloudVideo`
    this.remoteVideoId = `${this.masterIdPrefix}_${this.remoteUserId}_remoteVideo`
    this.containerId = `${this.masterIdPrefix}_${this.remoteUserId}_remoteVideoContainer`

    // 
    this.remoteVideo = this.createRemoteVideoElement()
  }

  public getVideoDomId(): string {
    //  DOM  ID
    return this.videoDomId
  }

  public getRemoteVideoId(): string {
    //  ID
    return this.remoteVideoId
  }

  public getContainerId(): string {
    //  ID
    return this.containerId
  }

  public getRemoteVideo(): HTMLVideoElement {
    // 
    return this.remoteVideo
  }

  public createElements(): HTMLElement {
    //  DOM 
    const newDiv = this.createDivElement(this.videoDomId) //  div
    const newDiv2 = this.createDiv("100%", "100%", "relative", "hidden") //  div
    const newDiv3 = this.createDiv("100%", "100%", "absolute", "hidden", this.containerId) //  div

    //  div 
    newDiv3.appendChild(this.remoteVideo)
    newDiv2.appendChild(newDiv3)
    newDiv.appendChild(newDiv2)

    return newDiv //  div
  }

  private createRemoteVideoElement(): HTMLVideoElement {
    // 
    const video = document.createElement("video")
    video.setAttribute("id", this.remoteVideoId)
    video.setAttribute("playsinline", "") //  inline 
    video.setAttribute("webkit-playsinline", "") // WebKit 
    video.setAttribute("x5-playsinline", "") // X5 
    video.setAttribute("x5-video-player-type", "h5") // X5 

    // 
    video.controls = false // 
    video.muted = true // 
    video.style.width = "100%" //  100%
    video.style.height = "100%" //  100%
    video.style.objectFit = "fill" // 

    return video // 
  }
  //  videoDomId 
  public bindDomEvent(type: string, listener: EventListener): void {
    const domElement = document.getElementById(this.videoDomId)
    if (domElement) {
      const wrappedListener = (event: Event) => {
        event.preventDefault() // 
        listener(event) // 
      }
      domElement.addEventListener(type, wrappedListener)
      // 
      this.eventListeners.push({ type, listener: wrappedListener, element: domElement })
    }
  }
  //  videoDomId 
  public removeAllEvents(): void {
    // 
    this.eventListeners.forEach(({ type, listener, element }) => {
      element.removeEventListener(type, listener)
    })
    // 
    this.eventListeners = []
  }

  private createDivElement(id: string): HTMLDivElement {
    //  ID  div 
    const div = document.createElement("div")
    div.setAttribute("id", id)
    return div //  div
  }

  private createDiv(width: string, height: string, position: string, overflow: string, id?: string): HTMLDivElement {
    //  div 
    const div = document.createElement("div")
    div.style.width = width // 
    div.style.height = height // 
    div.style.position = position // 
    div.style.overflow = overflow // 

    //  ID ID
    if (id) {
      div.setAttribute("id", id)
    }

    return div //  div
  }

  //  DOM
  public destroy(): void {
    // 1. 
    this.removeAllEvents()

    // 2.  DOM  videoDomId 
    const videoDomElement = document.getElementById(this.videoDomId)
    if (videoDomElement && videoDomElement.parentNode) {
      videoDomElement.parentNode.removeChild(videoDomElement)
    }

    // 3. 
    this.remoteVideo = null!
    this.eventListeners = []
  }
}
