const keyCodeMap: Record<string, number> = {
    ArrowUp: 19,
    ArrowDown: 20,
    ArrowLeft: 21,
    ArrowRight: 22,
    Enter: 66,
    Backspace: 67
}

/** input */
export const addInputElement = (rtc: any) => {
    // div
    const h5Dom = document.getElementById(rtc.initDomId)
    // input
    rtc.inputElement = document.createElement('input')
    // input
    rtc.inputElement.setAttribute('type', 'text')
    rtc.inputElement.setAttribute('id', `${rtc.masterIdPrefix}_${rtc.remoteUserId}_inputEle`)
    // inputstyle
    rtc.inputElement.setAttribute(
        'style',
        'position: absolute; top: 50px;left: 80px;z-index: 99; opacity: 0;background: transparent;'
    )

    // 
    let compositionstart = false
    rtc.inputElement.addEventListener('compositionstart', () => {
        compositionstart = true
    })
    rtc.inputElement.addEventListener('compositionend', (e: any) => {
        compositionstart = false
        // console.log('compositionstart', e)
        const messageObj = {
            action: 1,
            touchType: 'input',
            keyCode: 1,
            text: e.target.value
        }
        const userId = rtc.options.clientId
        const message = JSON.stringify(messageObj)
        rtc.inputElement.value = ''
        if (userId) rtc.sendUserMessage(userId, message)
    })
    rtc.inputElement.addEventListener('input', (e: any) => {
        if (compositionstart) return
        const messageObj = {
            action: 1,
            touchType: 'input',
            keyCode: 1,
            text: e.target.value
        }
        const userId = rtc.options.clientId
        const message = JSON.stringify(messageObj)
        rtc.inputElement.value = ''
        if (userId) rtc.sendUserMessage(userId, message)
    })
    rtc.inputElement.addEventListener('keydown', (e: KeyboardEvent) => {
        // console.log('keydown', e.key)
        const keyCode = keyCodeMap[e.key]
        if (keyCode !== undefined) {
            const messageObj = {
                action: 1,
                touchType: 'input',
                keyCode: keyCode,
                text: ''
            }
            const messageObj2 = {
                action: 0,
                touchType: 'input',
                keyCode: keyCode,
                text: ''
            }
            const userId = rtc.options.clientId
            const message = JSON.stringify(messageObj)
            const message2 = JSON.stringify(messageObj2)
            if (e.key == 'Enter') {
                // 
                rtc.inputElement.blur()
            }
            if (userId) {
                // 
                rtc.sendUserMessage(userId, message)
                // 
                rtc.sendUserMessage(userId, message2)
            }
        }
    })
    // input
    h5Dom?.appendChild(rtc.inputElement)
}
